  position: relative;
  height: 57px;
  overflow: hidden;
}

.accordeon-item:after {
  display: block;
  content: '';
  width: 24px;
  height: 24px;
  background: transparent url("../img/Small-Arrow-Down.svg") center no-repeat;
  background-size: contain;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 10;
}

.accordeon-item.active {
  height: auto;
}

.accordeon-item.active .accordeon-description {
  display: block;
  transform: translateY(0);
}

.accordeon-item.active:after {
  transform: rotate(180deg);
}

.accordeon-item table {
  display: block;
  border-radius: 2px;
  overflow: hidden;
  border: 1px solid #E9E9E9;
  margin-bottom: 30px;
}

.accordeon-item table:last-child {
  margin-bottom: 0;
}

.accordeon-item table tbody {
  display: block;
  width: 100%;
}

.accordeon-item table tr {
  width: 100%;
}

.accordeon-item table th, .accordeon-item table td {
  display: block;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  padding: 16px 4px;
  width: 12.5%;
}

.accordeon-item table th:nth-child(odd), .accordeon-item table td:nth-child(odd) {
  background: #E9E9E9;
}

.accordeon-item table td {
  padding-bottom: 16px;
}

.accordeon-item table td:last-child {
  padding-bottom: 0;
}

.accordeon-title {
  position: relative;
  cursor: pointer;
  background: #fff;
  padding-bottom: 30px;
  z-index: 10;
}

.accordeon-description {
  transform: translateY(-125%);
  padding-bottom: 30px;
}

.accordeon-description p {
  display: block;
  margin-bottom: 20px;
}

.accordeon-description p:last-child {
  margin-bottom: 0;
}

@media screen and (max-width: 500px) {
  .accordeon-item {
    margin-bottom: 20px;
  }
  .accordeon-item .accordeon-description {
    overflow-y: scroll;
  }
  .accordeon-item table {
    width: 150%;
    overflow: visible;
  }
  .accordeon-item table tbody {
    width: 150%;
    overflow-y: scroll;
  }
  .accordeon-item table th, .accordeon-item table td {
    font-size: 12px;
    line-height: 16px;
  }
  .accordeon-title {
    padding-bottom: 20px;
  }
  .accordeon-description {
    padding-bottom: 20px;
  }
}

body {
